"use client";

import { useState } from "react";
import { UploadInvoicesDialog } from "../components/UploadInvoicesDialog";
import { ShipmentCard } from "../components/ShipmentCard";
import { useI18n } from "@/i18n/i18nProvider";

type ShipmentMock = {
  id: string;
  trackingNumber: string;
  companyName: string;
  provider: string;
  price: number;
  currency: string;
  date: string;
  originCountry: string;
  destinationCountry: string;
  mode: "EXPORT" | "IMPORT";
  invoiceCount?: number;
};

const MOCK_SHIPMENTS: ShipmentMock[] = [
  {
    id: "1",
    trackingNumber: "885335999184",
    companyName: "Acme Corporation s.r.o.",
    provider: "FedEx",
    price: 457.24,
    currency: "Kč",
    date: "9. 10. 2025",
    originCountry: "CZ",
    destinationCountry: "UK",
    mode: "EXPORT",
    invoiceCount: 1,
  },
  {
    id: "2",
    trackingNumber: "885335999185",
    companyName: "Acme Corporation s.r.o.",
    provider: "FedEx",
    price: 457.24,
    currency: "Kč",
    date: "9. 10. 2025",
    originCountry: "UK",
    destinationCountry: "CZ",
    mode: "IMPORT",
    invoiceCount: 2,
  },
];

export default function DashboardPage() {
  const [companyFilter, setCompanyFilter] = useState("");
  const { t, locale, setLocale } = useI18n();

  const filtered = MOCK_SHIPMENTS.filter((s) =>
    companyFilter
      ? s.companyName.toLowerCase().includes(companyFilter.toLowerCase())
      : true
  );

  return (
    <main className="min-h-screen bg-slate-100 flex justify-center">
      <div className="w-full max-w-6xl px-4 py-6">
        {/* TOP BAR */}
        <div className="flex items-center justify-between mb-6">
          <div className="flex items-center gap-3">
            <input
              value={companyFilter}
              onChange={(e) => setCompanyFilter(e.target.value)}
              placeholder={t("dashboard.filterPlaceholder")}
              className="w-60 rounded-full border border-slate-200 bg-white px-3 py-2 text-sm text-slate-700 shadow-sm"
            />
          </div>

          <div className="flex items-center gap-3">
            {/* language switcher */}
            <div className="inline-flex items-center rounded-full bg-white border border-slate-200 px-2 py-1 text-xs shadow-sm">
              <button
                onClick={() => setLocale("en")}
                className={`px-2 py-0.5 rounded-full ${
                  locale === "en"
                    ? "bg-emerald-500 text-white"
                    : "text-slate-600"
                }`}
              >
                EN
              </button>

              <button
                onClick={() => setLocale("cs")}
                className={`px-2 py-0.5 rounded-full ${
                  locale === "cs"
                    ? "bg-emerald-500 text-white"
                    : "text-slate-600"
                }`}
              >
                CS
              </button>
            </div>

            <UploadInvoicesDialog />
          </div>
        </div>

        {/* SHIPMENTS */}
        <div className="grid grid-cols-1 sm:grid-cols-2 xl:grid-cols-3 gap-4">
          {filtered.map((s) => (
            <ShipmentCard key={s.id} {...s} />
          ))}

          {filtered.length === 0 && (
            <div className="col-span-full rounded-2xl bg-white border border-dashed border-slate-200 p-6 text-center text-sm text-slate-500">
              {t("dashboard.noShipments")}
            </div>
          )}
        </div>
      </div>
    </main>
  );
}
